import React, { Component } from "react";
import { Switch, Route } from "react-router-dom";
import DetailHotel from "./DetailHotel";
import CreateHotel from "./CreateHotel";
import ListHotel from "./ListHotel";

export default class HotelController extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <Switch>
        <Route exact path={`/admin/main/listhotel`} component={ListHotel} />
        <Route path={`/admin/main/listhotel/detail`} component={DetailHotel} />
        <Route path={`/admin/main/listhotel/create`} component={CreateHotel} />
      </Switch>
    );
  }
}
