import React, { Component } from "react";
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import Loading from "../../../../components/Loading";
import { TextField, CardMedia, Grid, Checkbox } from "@material-ui/core";
import {
  getDetailHotel,
  updateHotel,
  removeData,
  changeStatus
} from "../../../../redux/actions/detailHotelAction";


import { connect } from "react-redux";
import withStyles from "@material-ui/core/styles/withStyles";
import MySnackBar from "../../../../components/SnackBar";

const check = require('../../../../assets/img/check.png')
// const check = require('../../../../../public/img/check.png')

const unCheck = require('../../../../assets/img/uncheck.png')
// const unCheck = require("../../../../../public/img/uncheck.png");

class InfoDetail extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: "",
      rating: "",
      address: "",
      description: "",
      isUpdate: false,
      hotel: {},
      wifi: false,
      laundry: false,
      bar: false,
      active: false,
      status: false,
    };
  }

  componentWillMount() {
    const { id } = this.props;
    this.props.onGetDetail(id);
  }



  componentWillReceiveProps(nextProps) {
    if (nextProps && !!!nextProps.isChangeStatus && !!nextProps.changeStatusSuccess && !!!nextProps.errorChangeStatus) {
      this.openSnackBar("success", "Chuyn trng thi thnh cng");

    }
    if (nextProps && !!!nextProps.isChangeStatus && !!!nextProps.changeStatusSuccess && nextProps.errorChangeStatus) {
      this.openSnackBar("error", `Chuyn trng thi tht bi ! ${nextProps.errorChangeStatus}`);
    }

    if (nextProps && !!!nextProps.isUpdating && !!nextProps.updateSuccess && !!!nextProps.errorUpdate) {
      this.openSnackBar("success", "Cp nht thnh cng");

    }
    if (nextProps && !!!nextProps.isUpdating && !!!nextProps.updateSuccess && nextProps.errorUpdate) {
      this.openSnackBar("error", `Cp nht tht bi ! ${nextProps.errorUpdate}`);
    }

    if (nextProps && !!!nextProps.isLoadingHotel && !!!nextProps.getHotelSuccess && nextProps.errorGetHotel) {
      this.openSnackBar("error", `Ly thng tin khch sn tht bi ! ${nextProps.errorGetHotel}`);
    }
  }

  changeUpdateStatus = () => {
    const { detailHotel } = this.props;
    this.setState({
      isUpdate: true,
      name: detailHotel.name,
      address: detailHotel.address,
      description: detailHotel.description,
      wifi: detailHotel.facilities.wifi,
      laundry: detailHotel.facilities.laundry,
      bar: detailHotel.facilities.bar,
      active: detailHotel.active,
      status: detailHotel.status
    });
  };

  getBody = () => {
    let data = this.props.detailHotel;
    data.name = this.state.name;
    data.address = this.state.address;
    data.facilities.wifi = this.state.wifi;
    data.facilities.laundry = this.state.laundry;
    data.facilities.bar = this.state.bar;

    delete data.id;
    // delete data.active;
    delete data.point;
    delete data.ratings;

    return JSON.stringify(data);
  };

  update = () => {
    const { detailHotel, accessToken } = this.props;
    this.props.onUpdate(detailHotel.id, this.getBody(), accessToken);
  };

  getBodyUpdateStatus = () => {
    const params = {
      message: "string",
      status: !this.state.status
    };
    return JSON.stringify(params);
  };

  changeStatus = () => {
    const { accessToken } = this.props
    this.props.onChangeStatus(
      this.props.detailHotel.id,
      this.getBodyUpdateStatus(),
      accessToken
    );
  };

  openSnackBar = (type, message) => {
    const obj = {
      variant: type,
      message: message
    };
    this.refs.MySnackBar.handleOption(obj);
    this.refs.MySnackBar.handleOpen();
  };

  cancel = () => {
    this.setState({ isUpdate: false });
  };

  handleChange = name => {
    switch (name) {
      case "wifi":
        this.setState({ wifi: !this.state.wifi });
        break;
      case "laundry":
        this.setState({ laundry: !this.state.laundry });
        break;
      case "bar":
        this.setState({ bar: !this.state.bar });
        break;
      default:
        break;
    }
  };

  render() {
    const {
      classes,
      detailHotel,
      isLoadingHotel,
      isUpdating,
      isChangeStatus,
      onBack
    } = this.props;
    const {
      isUpdate,
      name,
      rating,
      status,
      active,
      address,
      description,
      wifi,
      laundry,
      bar,
    } = this.state;
    return (
      <Card>
        <CardHeader color="primary">
          <h4 className={classes.cardTitleWhite}>{detailHotel.name}</h4>
          <p className={classes.cardCategoryWhite}>Chi tit khch sn </p>
        </CardHeader>
        {isLoadingHotel ? (
          <Loading />
        ) : (
            <CardBody>
              <GridContainer>
                <GridItem xs={12} sm={12} md={5}>
                  <TextField
                    disabled={!isUpdate}
                    id="standard-name"
                    label="Tn khch sn "
                    value={isUpdate ? name : detailHotel.name}
                    onChange={event =>
                      this.setState({ name: event.target.value })
                    }
                    margin="normal"
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={3}>
                  <TextField
                    disabled={!isUpdate}
                    id="standard-address"
                    label="a ch "
                    value={isUpdate ? address : detailHotel.address}
                    onChange={event =>
                      this.setState({ address: event.target.value })
                    }
                    margin="normal"
                  />
                </GridItem>
              </GridContainer>
              <GridContainer
                style={{
                  alignItems: "center",
                  paddingTop: 15,
                  paddingBottom: 15
                }}
              >
                <h3
                  style={{
                    fontSize: 16,
                    marginLeft: "15px",
                    marginTop: 0,
                    marginBottom: 0,
                    padding: 0
                  }}
                >
                  Dch v cung cp
              </h3>
                <GridItem xs={12} sm={12} md={3}>
                  <Grid container alignItems="center" direction="row">
                    {isUpdate ? (
                      <Checkbox
                        checked={wifi}
                        onChange={() => this.handleChange("wifi")}
                      />
                    ) : (
                        <CardMedia
                          style={{ width: "20px", height: "20px" }}
                          image={detailHotel.facilities.wifi ? check : unCheck}
                          title="Paella dish"
                        />
                      )}
                    <h3
                      style={{
                        fontSize: 16,
                        padding: 0,
                        marginLeft: "20px",
                        marginBottom: 0,
                        marginTop: 0
                      }}
                    >
                      Wifi
                  </h3>
                  </Grid>
                </GridItem>
                <GridItem xs={12} sm={12} md={3}>
                  <Grid container alignItems="center" direction="row">
                    {isUpdate ? (
                      <Checkbox
                        checked={laundry}
                        onChange={() => this.handleChange("laundry")}
                      />
                    ) : (
                        <CardMedia
                          style={{ width: "20px", height: "20px" }}
                          image={detailHotel.facilities.laundry ? check : unCheck}
                          title="Paella dish"
                        />
                      )}
                    <h3
                      style={{
                        fontSize: 16,
                        padding: 0,
                        marginLeft: "20px",
                        marginBottom: 0,
                        marginTop: 0
                      }}
                    >
                      Laundry
                  </h3>
                  </Grid>
                </GridItem>
                <GridItem xs={12} sm={12} md={3}>
                  <Grid container alignItems="center" direction="row">
                    {isUpdate ? (
                      <Checkbox
                        checked={bar}
                        onChange={() => this.handleChange("bar")}
                      />
                    ) : (
                        <CardMedia
                          style={{ width: "20px", height: "20px" }}
                          image={detailHotel.facilities.bar ? check : unCheck}
                          title="Paella dish"
                        />
                      )}
                    <h3
                      style={{
                        fontSize: 16,
                        padding: 0,
                        marginLeft: "20px",
                        marginBottom: 0,
                        marginTop: 0
                      }}
                    >
                      Bar
                  </h3>
                  </Grid>
                </GridItem>
              </GridContainer>
              <GridContainer
                style={{
                  alignItems: "center",
                  paddingTop: 15,
                  paddingBottom: 15
                }}
              >
                <h3
                  style={{
                    fontSize: 16,
                    marginLeft: "15px",
                    marginTop: 0,
                    marginBottom: 0,
                    padding: 0
                  }}
                >
                  Trng thi
              </h3>
                <GridItem xs={12} sm={12} md={4}>
                  <Grid container alignItems="center" direction="row">
                    <CardMedia
                      style={{ width: "20px", height: "20px" }}
                      image={detailHotel.active ? check : unCheck}
                      title="Paella dish"
                    />
                    <h3
                      style={{
                        fontSize: 16,
                        padding: 0,
                        marginLeft: "20px",
                        marginBottom: 0,
                        marginTop: 0
                      }}
                    >
                      ang hot ng
                  </h3>
                  </Grid>
                </GridItem>
                <GridItem xs={12} sm={12} md={4}>
                  <Grid container alignItems="center" direction="row">
                    <CardMedia
                      style={{ width: "20px", height: "20px" }}
                      image={detailHotel.status ? check : unCheck}
                      title="Paella dish"
                    />
                    <h3
                      style={{
                        fontSize: 16,
                        padding: 0,
                        marginLeft: "20px",
                        marginBottom: 0,
                        marginTop: 0
                      }}
                    >
                      Cn phng
                  </h3>
                  </Grid>
                </GridItem>
              </GridContainer>
              <GridContainer
                style={{
                  alignItems: "center",
                  paddingTop: 15,
                  paddingBottom: 15
                }}
              >
                <h3
                  style={{
                    fontSize: 16,
                    marginLeft: "15px",
                    marginTop: 0,
                    marginBottom: 0,
                    padding: 0
                  }}
                >
                  {`S lt nh gi : ${detailHotel.ratings}`}
                </h3>
              </GridContainer>
              <GridContainer
                style={{
                  alignItems: "center",
                  paddingTop: 15,
                  paddingBottom: 15
                }}
              >
                <h3
                  style={{
                    fontSize: 16,
                    marginLeft: "15px",
                    marginTop: 0,
                    marginBottom: 0,
                    padding: 0
                  }}
                >
                  {`im  : ${detailHotel.point} / 5`}
                </h3>
              </GridContainer>
              <GridContainer
                style={{
                  alignItems: "center",
                  paddingTop: 15,
                  paddingBottom: 15
                }}
              >
                <h3
                  style={{
                    fontSize: 16,
                    marginLeft: "15px",
                    marginTop: 0,
                    marginBottom: 0,
                    padding: 0
                  }}
                >
                  {`S in thoi  : ${detailHotel.phone_number ? detailHotel.phone_number : "Cha c"}`}
                </h3>
              </GridContainer>
              <GridContainer>
                <GridItem xs={12} sm={12} md={12}>
                  <TextField
                    disabled={!isUpdate}
                    fullWidth={true}
                    id="standard-description"
                    label="Description"
                    value={isUpdate ? description : detailHotel.description}
                    onChange={event =>
                      this.setState({ description: event.target.value })
                    }
                    margin="normal"
                  />
                </GridItem>
              </GridContainer>
            </CardBody>
          )}
        <CardFooter>
          {!isUpdate ? (
            <Grid container direction="row">
              <Button
                disabled={isUpdating || isChangeStatus || isLoadingHotel}
                style={{ marginRight: 20 }}
                color="primary"
                onClick={() => this.changeUpdateStatus()}
              >
                Cp nht
              </Button>
              <Button
                disabled={isUpdating || isChangeStatus || isLoadingHotel}
                color="primary"
                style={{ marginRight: 20 }}
                onClick={this.changeStatus}
              >
                Chuyn trng thi
              </Button>
              <Button
                disabled={isUpdating || isChangeStatus || isLoadingHotel}
                color="primary"
                onClick={onBack}
              >
                Quay li
              </Button>
            </Grid>
          ) : (
              <Grid container direction="row">
                <Button style={{ marginRight: 20 }} color="primary" onClick={() => this.update()}>
                  Xong
              </Button>
                <Button
                  color="primary"
                  onClick={() => this.cancel()}
                >
                  Quay li
              </Button>
              </Grid>
            )}
        </CardFooter>
        <MySnackBar ref={"MySnackBar"} />
      </Card>
    );
  }
}

const mapStateToProps = state => {
  return {
    accessToken: state.profileReducer.accessToken,

    detailHotel: state.detailHotelReducer.detailHotel,
    isLoadingHotel: state.detailHotelReducer.isLoadingHotel,
    getHotelSuccess: state.detailHotelReducer.getHotelSuccess,
    errorGetHotel: state.detailHotelReducer.errorGetHotel,

    isUpdating: state.detailHotelReducer.isUpdating,
    updateSuccess: state.detailHotelReducer.updateSuccess,
    errorUpdate: state.detailHotelReducer.errorUpdate,

    isChangeStatus: state.detailHotelReducer.isChangeStatus,
    errorChangeStatus: state.detailHotelReducer.errorChangeStatus,
    changeStatusSuccess: state.detailHotelReducer.changeStatusSuccess
  };
};

const styles = {
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0"
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none"
  }
};

const mapDispatchToProps = dispatch => {
  return {
    onGetDetail: id => {
      dispatch(getDetailHotel(id));
    },
    onUpdate: (id, body, token) => {
      dispatch(updateHotel(id, body, token));
    },
    onRemove: () => {
      dispatch(removeData());
    },
    onChangeStatus: (id, body, token) => {
      dispatch(changeStatus(id, body, token));
    }
  };
};

const redux = connect(
  mapStateToProps,
  mapDispatchToProps
)(InfoDetail);

export default withStyles(styles)(redux);
