import React from "react";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
import { connect } from 'react-redux';
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardAvatar from "components/Card/CardAvatar.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";

import avatar from "assets/img/faces/marc.jpg";
import { getUserDetail, updateUser, deleteUser, removeNow } from "../../../redux/actions/detailUserAction";
import { TextField, Grid, MenuItem } from "@material-ui/core";
import Loading from "../../../components/Loading";
import ManagerHotels from "./ManagerHotels";
import { MANAGER } from "../../../config/constAct";
import MySnackBar from "../../../components/SnackBar";

const roles = [
    {
        label: 'MANAGER',
    },
    {
        label: 'CLIENT',
    },
];

class UserProfile extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            id: props.history.location.state.id,
            isUpdate: false,
            username: '',
            name: '',
            full_name: '',
            role: '',
            email: '',
            phone: ''
        }
    }
    componentDidMount() {
        this.props.onGetUserDetail(this.state.id)
    }

    componentWillUnmount(){
        console.log('unmount')
        this.props.onRemove()
    }

    updateProfile = () => {
        const { userDetail, accessToken } = this.props
        this.setState({ isUpdate: !this.state.isUpdate })
        this.props.onUpdateUser(userDetail.id, this.bodyUpdate(), accessToken)
    }

    bodyUpdate = () => {
        const { full_name, email, phone, name } = this.state
        const body = {
            email: email,
            full_name: full_name,
            name: name,
            phone: phone,
        }

        console.log('body', body)

        return JSON.stringify(body)
    }

    changeForm = () => {
        const { userDetail } = this.props
        const { isUpdate } = this.state;
        this.setState({
            username: userDetail.username,
            name: userDetail.name,
            full_name: userDetail.full_name,
            email: userDetail.email,
            phone: userDetail.phone,
            isUpdate: !isUpdate
        })

    }

    componentWillReceiveProps(nextProps) {
        if (nextProps && !!!nextProps.isUpdate && !!nextProps.updateSuccess && !!!nextProps.errorUpdate) {
            this.openSnackBar("success", "Cp nht thnh cng");
            // this.onBack();
        }
        if (nextProps && !!!nextProps.isUpdate && !!!nextProps.updateSuccess && nextProps.errorUpdate) {
            this.openSnackBar("error", `Cp nht tht bi ! ${nextProps.errorUpdate}`);
        }
    }

    openSnackBar = (type, message) => {
        const obj = {
            variant: type,
            message: message
        };
        this.refs.MySnackBar.handleOption(obj);
        this.refs.MySnackBar.handleOpen();
    };

    delete = () => {
        const { userDetail, accessToken } = this.props
        this.props.onDeleteUser(userDetail.id, accessToken)
    }

    handleChange = event => {
        this.setState({ role: event.target.value })
    }

    render() {
        const { classes, userDetail, isGetUserDetail, history } = this.props;
        const { isUpdate, username, full_name, role, email, phone, name } = this.state
        return (
            <div>
                <GridContainer>
                    <GridItem xs={12} sm={12} md={isUpdate ? 12 : 8}>
                        <Card>
                            <CardHeader color="primary">
                                <h4 className={classes.cardTitleWhite}>{isUpdate ? `Chnh sa thng tin` : "Thng tin"}</h4>
                                <p className={classes.cardCategoryWhite}>Complete your profile</p>
                            </CardHeader>
                            {isGetUserDetail ? <Loading />
                                : <CardBody>
                                    <GridContainer>
                                        <GridItem xs={12} sm={12} md={8}>
                                            <TextField
                                                disabled={true}
                                                id="standard-name"
                                                label="Ti khon "
                                                value={isUpdate ? username : userDetail.username}
                                                onChange={event =>
                                                    this.setState({ username: event.target.value })
                                                }
                                                margin="normal"
                                                fullWidth
                                            />
                                        </GridItem>

                                        <GridItem xs={12} sm={12} md={4}>
                                            <TextField
                                                id="standard-select-currency"
                                                select
                                                label="Chn quyn"
                                                disabled={true}
                                                value={userDetail.role}
                                                onChange={this.handleChange}
                                                SelectProps={{
                                                    MenuProps: {
                                                        className: classes.menu,
                                                    },
                                                }}
                                                margin="normal"
                                                fullWidth
                                            >
                                                {roles.map(option => (
                                                    <MenuItem key={option.label} value={option.label}>
                                                        {option.label}
                                                    </MenuItem>
                                                ))}
                                            </TextField>
                                        </GridItem>
                                    </GridContainer>
                                    <GridContainer>
                                        <GridItem xs={12} sm={12} md={6}>
                                            <TextField
                                                disabled={!isUpdate}
                                                id="standard-name"
                                                label="Tn"
                                                value={isUpdate ? name : userDetail.name}
                                                onChange={event =>
                                                    this.setState({ name: event.target.value })
                                                }
                                                margin="normal"
                                                fullWidth
                                            />
                                        </GridItem>
                                        <GridItem xs={12} sm={12} md={6}>
                                            <TextField
                                                disabled={!isUpdate}
                                                id="standard-name"
                                                label="Tn y "
                                                value={isUpdate ? full_name : userDetail.full_name}
                                                onChange={event =>
                                                    this.setState({ full_name: event.target.value })
                                                }
                                                margin="normal"
                                                fullWidth
                                            />
                                        </GridItem>

                                    </GridContainer>
                                    <GridContainer>
                                        <GridItem xs={12} sm={12} md={6}>
                                            <TextField
                                                disabled={!isUpdate}
                                                id="standard-name"
                                                label="Email"
                                                value={isUpdate ? email : userDetail.email}
                                                onChange={event =>
                                                    this.setState({ email: event.target.value })
                                                }
                                                margin="normal"
                                                fullWidth
                                            />
                                        </GridItem>
                                        <GridItem xs={12} sm={12} md={6}>
                                            <TextField
                                                disabled={!isUpdate}
                                                id="standard-name"
                                                label="S in thoi"
                                                value={isUpdate ? phone : userDetail.phone}
                                                onChange={event =>
                                                    this.setState({ phone: event.target.value })
                                                }
                                                margin="normal"
                                                fullWidth
                                            />
                                        </GridItem>
                                    </GridContainer>
                                </CardBody>}
                            <CardFooter>
                                {isUpdate ? <Button onClick={this.updateProfile} color="primary">Lu</Button>
                                    : <Grid>
                                        <Button onClick={this.changeForm} color="primary">Cp nht thng tin</Button>
                                        <Button style={{ marginLeft: 20, }} onClick={this.delete} color="primary">Xo ti khon</Button>
                                    </Grid>}

                            </CardFooter>
                        </Card>
                    </GridItem>
                    {!isUpdate && <GridItem xs={12} sm={12} md={4}>
                        <Card profile>
                            <CardAvatar profile>
                                <a href="#pablo" onClick={e => e.preventDefault()}>
                                    <img src={userDetail.picture ? userDetail.picture : avatar} alt="..." />
                                </a>
                            </CardAvatar>
                            <CardBody>
                                <h6 className={classes.cardCategory}>{userDetail.full_name}</h6>
                                <Grid style={{ display: 'flex', alignItems: 'flex-start', flexDirection: 'column' }}>
                                    <h7 >{`St : ${userDetail.phone}`}</h7>
                                    <h7 >{`Quyn : ${userDetail.role}`}</h7>
                                    <h7 >{`Email : ${userDetail.email}`}</h7>
                                </Grid>

                            </CardBody>
                        </Card>
                    </GridItem>}
                    {userDetail && userDetail.id && history.location.state.type === MANAGER &&
                        <GridItem xs={12} sm={12} md={12}>
                            <ManagerHotels id={userDetail.id} classes={classes} />
                        </GridItem>
                    }
                </GridContainer>
                <MySnackBar ref={"MySnackBar"} />
            </div>
        );
    }
}


const styles = {
    cardCategoryWhite: {
        color: "rgba(255,255,255,.62)",
        margin: "0",
        fontSize: "14px",
        marginTop: "0",
        marginBottom: "0"
    },
    cardTitleWhite: {
        color: "#FFFFFF",
        marginTop: "0px",
        minHeight: "auto",
        fontWeight: "300",
        fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
        marginBottom: "3px",
        textDecoration: "none"
    },
    menu: {
        width: 200
    }
};

const mapStateToProps = state => {
    return {
        profile: state.profileReducer.profile,
        accessToken: state.profileReducer.accessToken,
        userDetail: state.userDetailReducer.userDetail,
        isGetUserDetail: state.userDetailReducer.isGetUserDetail,
        getDetailSuccess: state.userDetailReducer.getDetailSuccess,
        errorGetDetail: state.userDetailReducer.errorGetDetail,

        isUpdate: state.userDetailReducer.isUpdate,
        updateSuccess: state.userDetailReducer.updateSuccess,
        errorUpdate: state.userDetailReducer.errorUpdate,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onGetUserDetail: id => {
            dispatch(getUserDetail(id))
        },
        onUpdateUser: (id, body, token) => {
            dispatch(updateUser(id, body, token))
        },
        onDeleteUser: (id, token) => {
            dispatch(deleteUser(id, token))
        },
        onRemove : () => {
            dispatch(removeNow())
        }
    }
}

const redux = connect(mapStateToProps, mapDispatchToProps)(UserProfile)

export default withStyles(styles)(redux);
