import {
  GET_DETAIL_HOTEL_FAILED,
  GET_DETAIL_HOTEL_SUCCESS,
  GET_DETAIL_HOTEL_REQUEST,
  GET_ROOMS_HOTEL_REQUEST,
  GET_ROOMS_HOTEL_SUCCESS,
  GET_ROOMS_HOTEL_FAILED,
  UPDATE_HOTEL_REQUEST,
  UPDATE_HOTEL_FAILED,
  UPDATE_HOTEL_SUCCESS,
  REMOVE_DETAIL,
  CHANGE_STATUS_REQUEST,
  CHANGE_STATUS_SUCCESS,
  CHANGE_STATUS_FAILED,
  GET_REVIEW_SUCCESS,
  GET_REVIEW_REQUEST,
  GET_REVIEW_FAILED,
  CREATE_HOTEL_REQUEST,
  CREATE_HOTEL_SUCCESS,
  CREATE_HOTEL_FAILED,
  LOG_OUT
} from "../../config/constAct";

const initialState = {
  detailHotel: {},
  isLoadingHotel: true,
  getHotelSuccess: false,
  errorGetHotel: null,

  rooms: [],
  isLoadingRooms: false,
  getRoomsSuccess: false,
  errorGetRooms: null,

  //update
  isUpdating: false,
  updateSuccess: false,
  errorUpdate: null,

  //change status
  isChangeStatus: false,
  changeStatusSuccess: false,
  errorChangeStatus: null,

  total_reviews: 0,
  isLoadingReview: false,
  review: [],
  getReviewSuccess: false,
  errorGetReview: null,

  isCreating: false,
  createSuccess: false,
  createError: null
};

const detailHotelReducer = (state = initialState, action) => {
  switch (action.type) {
    //GET DETAIL HOTEL
    case GET_DETAIL_HOTEL_REQUEST:
      return Object.assign({}, state, {
        isLoadingHotel: true,
        getHotelSuccess: false,
        errorGetHotel: null,
      });
    case GET_DETAIL_HOTEL_SUCCESS:
      return Object.assign({}, state, {
        isLoadingHotel: false,
        getHotelSuccess: true,
        errorGetHotel: null,
        detailHotel: action.payload
      });

    case GET_DETAIL_HOTEL_FAILED:
      return Object.assign({}, state, {
        isLoadingHotel: false,
        getHotelSuccess: false,
        errorGetHotel: action.payload,
        detailHotel: {}
      });

    //CREATE HOTEL
    case CREATE_HOTEL_REQUEST:
      return Object.assign({}, state, {
        isCreating: true,
        createSuccess: false,
        createError: null
      })
    case CREATE_HOTEL_SUCCESS:
      return Object.assign({}, state, {
        isCreating: false,
        createSuccess: true,
        createError: null
      })
    case CREATE_HOTEL_FAILED:
      return Object.assign({}, state, {
        isCreating: false,
        createSuccess: false,
        createError: action.payload
      })

    //GET ROOMS
    case GET_ROOMS_HOTEL_REQUEST:
      return Object.assign({}, state, {
        rooms: [],
        isLoadingRooms: true,
        getRoomsSuccess: false,
        errorGetRooms: null,
      });

    case GET_ROOMS_HOTEL_SUCCESS:
      return Object.assign({}, state, {
        rooms: action.payload,
        isLoadingRooms: false,
        getRoomsSuccess: true,
        errorGetRooms: null,
      });

    case GET_ROOMS_HOTEL_FAILED:
      return Object.assign({}, state, {
        rooms: [],
        isLoadingRooms: false,
        getRoomsSuccess: false,
        errorGetRooms: action.payload,
      });

    //UPDATE HOTEL
    case UPDATE_HOTEL_REQUEST:
      return Object.assign({}, state, {
        isUpdating: true,
        updateSuccess: false,
        errorUpdate: null
      });

    case UPDATE_HOTEL_SUCCESS:
      return Object.assign({}, state, {
        isUpdating: false,
        updateSuccess: true,
        errorUpdate: null,
        detailHotel: action.payload
      });

    case UPDATE_HOTEL_FAILED:
      return Object.assign({}, state, {
        isUpdating: false,
        updateSuccess: false,
        errorUpdate: action.payload
      });

    //GET REVIEW
    case GET_REVIEW_REQUEST:
      return Object.assign({}, state, {
        isLoadingReview: true,
        review: [],
        getReviewSuccess: false,
        errorGetReview: null
      });

    case GET_REVIEW_SUCCESS:
      return Object.assign({}, state, {
        isLoadingReview: false,
        review: action.payload,
        getReviewSuccess: true,
        errorGetReview: null
      });

    case GET_REVIEW_FAILED:
      return Object.assign({}, state, {
        isLoadingReview: false,
        review: [],
        getReviewSuccess: false,
        errorGetReview: action.payload
      });

    //CHANGE STATUS
    case CHANGE_STATUS_REQUEST:
      return Object.assign({}, state, {
        changeStatusSuccess: false,
        errorChangeStatus: null,
        isChangeStatus: true
      });

    case CHANGE_STATUS_SUCCESS:
      return Object.assign({}, state, {
        detailHotel: {
          ...state.detailHotel,
          status: !state.detailHotel.status
        },
        isChangeStatus: false,
        changeStatusSuccess: true,
        errorChangeStatus: null
      });

    case CHANGE_STATUS_FAILED:
      return Object.assign({}, state, {
        isChangeStatus: false,
        changeStatusSuccess: false,
        errorChangeStatus: action.payload
      });

    //DELETE
    case REMOVE_DETAIL:
      return initialState;

    case LOG_OUT:
      return initialState;

    default:
      return state;
  }
};

export default detailHotelReducer;
