import React from "react";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import Button from "components/CustomButtons/Button.js";
import {
  Grid,
  IconButton,
  Paper,
  InputBase
} from "@material-ui/core";
import CustomTable from "../../../components/CustomTable";
import FilterListIcon from "@material-ui/icons/FilterList";
import SearchIcon from "@material-ui/icons/Search";
import { connect } from "react-redux";
import {
  getListHotel,
  deleteHotel
} from "../../../redux/actions/listHotelAction";
import MySnackBar from "../../../components/SnackBar";
import Loading from "../../../components/Loading";
import TableHotel from "../../../components/Table/TableHotel";

class ListHotel extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isFilter: false,
      selected: [],
      currentPage: 0
    };
  }

  componentDidMount() {
    this.props.onGetListHotel(this.state.currentPage);
  }

  onSelectTable = detail => {
    this.navigate("/admin/main/listhotel/detail", detail.id);
  };

  onCreate = () => {
    this.navigate("/admin/main/listhotel/create");
  };

  onDelete = () => {
    const { selected } = this.state;
    this.props.onDeleteHotel(selected[0]);
    this.openSnackBar("success", "Xo thnh cng");
  };

  navigate = (name, param) => {
    this.props.history.push({
      pathname: name,
      state: {
        id: param
      }
    });
  };

  getSelectedList = list => {
    this.setState({ selected: list });
  };

  openSnackBar = (type, message) => {
    const obj = {
      variant: type,
      message: message
    };
    this.refs.MySnackBar.handleOption(obj);
    this.refs.MySnackBar.handleOpen();
  };

  onNext = () => {
    this.setState({ currentPage: this.state.currentPage + 1 }, () => {
      let arr = this.props.listHotel.filter(item => item.page === this.state.currentPage)
      if (arr.length === 0) {
        this.props.onGetListHotel(this.state.currentPage);
      }
    })
  }
  componentWillReceiveProps(nextProps) {
    if (nextProps && !!!nextProps.isLoading && !!!nextProps.getSuccess && nextProps.errorGet) {
      this.openSnackBar("error", `Cp nht tht bi ! ${nextProps.errorGet}`);
    }
  }

  onPrevious = () => {
    this.setState({ currentPage: this.state.currentPage - 1 })
  }

  render() {
    const { classes, listHotel, isLoading } = this.props;
    const { isFilter, selected, currentPage } = this.state;
    return (
      <GridContainer>
        <GridItem xs={12} sm={12} md={12}>
          <Card>
            <CardHeader color="primary">
              <h4 className={classes.cardTitleWhite}>
                Danh sch cc khch sn
              </h4>
            </CardHeader>
            <CardBody>
              <Grid
                style={{
                  display: "flex",
                  justifyContent: "space-between"
                }}
              >
                <Paper className={classes.root}>
                  <InputBase
                    className={classes.input}
                    placeholder="Tm kim"
                    onChange={(event => console.log('event', event.target.value))}
                  />
                  <IconButton
                    className={classes.iconButton}
                    aria-label="Search"
                  >
                    <SearchIcon />
                  </IconButton>
                </Paper>
                <IconButton
                  aria-label="Filter list"
                  onClick={() =>
                    this.setState({ isFilter: !this.state.isFilter })
                  }
                >
                  <FilterListIcon />
                  <h5 className={classes.cardTitle}>
                    {isFilter ? "Hu sp xp" : "Sp xp"}
                  </h5>
                </IconButton>
              </Grid>

              {isLoading ? (
                <Loading />
              ) : isFilter ? (
                <CustomTable
                  callBackList={this.getSelectedList}
                  currentPage={currentPage}
                  // data={listHotel}
                  onPrevious={this.onPrevious}
                  onNext={this.onNext}
                  rows={[
                    {
                      id: "name",
                      label: "Tn"
                    },
                    {
                      id: "address",
                      label: "a ch"
                    },
                    {
                      id: "ratings",
                      label: "Lt nh gi"
                    },
                    {
                      id: "point",
                      label: "im"
                    },
                    {
                      id: "description",
                      label: "M t"
                    }
                  ]}
                />
              ) : (
                    <TableHotel
                      onSelectTable={this.onSelectTable}
                      tableHeaderColor="primary"
                      tableHead={[
                        "Tn ",
                        "a ch",
                        "Lt nh gi",
                        "im",
                        "M t"
                      ]}
                      currentPage={currentPage}
                      onPrevious={this.onPrevious}
                      onNext={this.onNext}
                    />
                  )}
            </CardBody>
            <CardFooter
              style={{ justifyContent: "flex-end", alignItems: "flex-end" }}
            >
              {isFilter && selected.length > 0 && (
                <Button
                  style={{ marginRight: 20 }}
                  color="primary"
                  onClick={this.onDelete}
                >
                  Xo
                </Button>
              )}
              <Button color="primary" onClick={this.onCreate}>
                To mi
              </Button>
            </CardFooter>
          </Card>
        </GridItem>
        <MySnackBar ref={"MySnackBar"} />
      </GridContainer>
    );
  }
}

const styles = {
  cardCategoryWhite: {
    "&,& a,& a:hover,& a:focus": {
      color: "rgba(255,255,255,.62)",
      margin: "0",
      fontSize: "14px",
      marginTop: "0",
      marginBottom: "0"
    },
    "& a,& a:hover,& a:focus": {
      color: "#FFFFFF"
    }
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
    "& small": {
      color: "#777",
      fontSize: "65%",
      fontWeight: "400",
      lineHeight: "1"
    }
  },
  cardTitle: {
    color: "#00000",
    marginTop: "0px",
    marginLeft: "10px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
    fontSize: 18
  },
  root: {
    padding: "2px 4px",
    display: "flex",
    alignItems: "center",
    width: '100%'
  },
  input: {
    marginLeft: 8,
    flex: 1
  },
  iconButton: {
    padding: 10
  },
  divider: {
    width: 1,
    height: 28,
    margin: 4
  }
};

const mapStateToProps = state => {
  return {
    listHotel: state.listHotelReducer.listHotel,
    isLoading: state.listHotelReducer.isLoading,
    getSuccess: state.listHotelReducer.getSuccess,
    errorGet: state.listHotelReducer.errorGet,
    total_pages: state.listHotelReducer.total_pages,
  };
};

const mapDispatchToProps = dispatch => {
  return {
    onGetListHotel: page => {
      dispatch(getListHotel(page));
    },
    onDeleteHotel: id => {
      dispatch(deleteHotel(id));
    }
  };
};

const redux = connect(
  mapStateToProps,
  mapDispatchToProps
)(ListHotel);

export default withStyles(styles)(redux);
