import { GET_LIST_HOTEL_REQUEST, GET_LIST_HOTEL_SUCCESS, GET_LIST_HOTEL_FAILED, DELETE_HOTEL_REQUEST, DELETE_HOTEL_SUCCESS, DELETE_HOTEL_FAILED, SEARCH_SUCCESS, SEARCH_FAILED, SEARCH_REQUEST, LOG_OUT } from "../../config/constAct";

const initialState = {
    listHotel: [],
    isLoading: true,
    getSuccess: false,
    errorGet: null,

    total_pages: 0,


    listSearch: [],
    isSearch: false,
    searchSuccess: false,
    errorSearch: null
}

// listHotel = [
//     {
//         page: 1,
//         content: []
//             ``
//     },
//     {
//         page: 2,
//         content: []
//     }
// ]

const listHotelReducer = (state = initialState, action) => {
    switch (action.type) {
        //GET LIST HOTEL
        case GET_LIST_HOTEL_REQUEST:
            return Object.assign({}, state, {
                // listHotel:[],
                isLoading: true,
                getSuccess: false,
                errorGet: null,
            })
        case GET_LIST_HOTEL_SUCCESS:
            const arr = action.payload
            const newPage = {
                page: arr.number,
                content: arr.content
            }
            return {
                listHotel: state.listHotel.concat(newPage),
                total_pages: arr.total_pages,
                isLoading: false,
                getSuccess: true,
                errorGet: null,
            }

        case GET_LIST_HOTEL_FAILED:
            return Object.assign({}, state, {
                // listHotel:[],
                isLoading: false,
                getSuccess: false,
                errorGet: action.payload,
            })

        //DELETE HOTEL
        case DELETE_HOTEL_REQUEST:
            return Object.assign({}, state, {
                isLoading: true,
            })
        case DELETE_HOTEL_SUCCESS:
            return Object.assign({}, state, {
                listHotel: state.listHotel.filter(item => item.id !== action.payload),
            })

        case DELETE_HOTEL_FAILED:
            return Object.assign({}, state, {
                isLoading: false,
            })

        //SEARCH 
        case SEARCH_REQUEST:
            return Object.assign({}, state, {
                listSearch: [],
                isSearch: true,
                searchSuccess: false,
                errorSearch: null
            })
        case SEARCH_SUCCESS:
            return Object.assign({}, state, {
                listSearch: action.payload,
                isSearch: false,
                searchSuccess: true,
                errorSearch: null
            })
        case SEARCH_FAILED:
            return Object.assign({}, state, {
                listSearch: [],
                isSearch: true,
                searchSuccess: false,
                errorSearch: action.payload
            })

        case LOG_OUT:
            return initialState;

        default:
            return state;
    }
}

export default listHotelReducer;