export const searchHotelByNameRequest = () => ({
    type: SEARCH_REQUEST
})

export const searchHotelByNameSuccess = (data) => ({
    type: SEARCH_SUCCESS,
    payload: data
})

export const searchHotelByNameFailed = (error) => ({
    type: SEARCH_FAILED,
    payload: error
})


export const searchHotelByName = (name) => {
    return async dispatch => {
        dispatch(searchHotelByNameRequest())
        try {
            let config = {
                method: 'GET',
                headers: {
                    'Accept': 'application/json',
                    "Content-Type": "application/json",
                },
            }
            let response = await fetch(`${HOST}/hotels?name=${name}`, config)
            let json = await response.json()
            console.log('searchByName', json)
            let arr = json.content
            if (json.content) {
                dispatch(searchHotelByNameSuccess(arr))
            } else {
                dispatch(searchHotelByNameFailed(json.message))
            }

        } catch (error) {
            console.log('error', error)
            dispatch(searchHotelByNameFailed(error))
        }
    }
}
