import {
  GET_LIST_USER_FAILED,
  HOST,
  GET_LIST_USER_REQUEST,
  GET_LIST_USER_SUCCESS,
} from "../../config/constAct";

//GET LIST USER
export const getListUserRequest = () => ({
  type: GET_LIST_USER_REQUEST
});

export const getListUserSuccess = data => ({
  type: GET_LIST_USER_SUCCESS,
  payload: data
});

export const getListUserFailed = error => ({
  type: GET_LIST_USER_FAILED,
  payload: error
});

export const getListUser = token => {
  return async dispatch => {
    dispatch(getListUserRequest());
    try {
      let config = {
        method: "GET",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
          Authorization: "Bearer " + token
        }
      };
      console.log('token',token)
      let response = await fetch(`${HOST}/users?page_size=30`, config);
      let json = await response.json();
      console.log("get", json);
      if (json.content) {
        dispatch(getListUserSuccess(json.content));
      } else {
        dispatch(getListUserFailed(json.message));
      }
    } catch (error) {
      console.log("error", error);
      dispatch(getListUserFailed(error));
    }
  };
};

