import React from "react";
import PropTypes from "prop-types";
// @material-ui/core components
import { connect } from 'react-redux';
import withStyles from "@material-ui/core/styles/withStyles";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import KeyboardArrowRight from "@material-ui/icons/KeyboardArrowRight";
import KeyboardArrowLeft from "@material-ui/icons/KeyboardArrowLeft";
// core components
import tableStyle from "assets/jss/material-dashboard-react/components/tableStyle.js";
import { Grid, IconButton } from "@material-ui/core";
class TableHotel extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      page: 0,
    };
  }

  render() {
    const {
      classes,
      tableHead,
      tableHeaderColor,
      onSelectTable,
      isManager,
      currentPage,
      total_pages,
      listHotel,
      onPrevious,
      onNext,
      isLoading
    } = this.props;
    const arr = listHotel[currentPage]
    console.log('listHotel',listHotel)
    console.log('currentPage',currentPage)
    console.log('tabledata',arr)
    if (arr) {
      const tableData = arr.content
      return (
        <div className={classes.tableResponsive}>
          <Table className={classes.table}>
            {tableHead !== undefined ? (
              <TableHead className={classes[tableHeaderColor + "TableHeader"]}>
                <TableRow>
                  {tableHead.map((prop, key) => {
                    return (
                      <TableCell
                        className={
                          classes.tableCell + " " + classes.tableHeadCell
                        }
                        key={key}
                      >
                        {prop}
                      </TableCell>
                    );
                  })}
                </TableRow>
              </TableHead>
            ) : null}
            <TableBody>
              {tableData
                .map((prop, key) => {
                  return (
                    <TableRow key={key} onClick={() => onSelectTable(prop)}>
                      <TableCell className={classes.tableCell}>
                        {isManager ? prop.hotel.name : prop.name}
                      </TableCell>
                      <TableCell className={classes.tableCell}>
                        {isManager ? prop.hotel.address : prop.address}
                      </TableCell>
                      <TableCell className={classes.tableCell}>
                        {isManager ? prop.hotel.ratings : prop.ratings}
                      </TableCell>
                      <TableCell className={classes.tableCell}>
                        {isManager ? prop.hotel.point + "/5" : prop.point + "/5"}
                      </TableCell>
                      <TableCell className={classes.tableCell}>
                        {isManager ? prop.hotel.description : prop.description}
                      </TableCell>
                    </TableRow>
                  );
                })}
            </TableBody>
          </Table>
          <Grid style={{ display: 'flex', flexDirection: 'row', justifyContent: 'flex-end' }}>
            <h4 style={{ fontSize: 14 }}>{`Trang ${currentPage + 1} / ${total_pages}`}</h4>
            <IconButton disabled={currentPage === 0 || isLoading ? true : false}>
              <KeyboardArrowLeft onClick={onPrevious} />
            </IconButton>
            <IconButton disabled={currentPage === total_pages - 1 || isLoading  ? true : false} onClick={onNext}>
              <KeyboardArrowRight />
            </IconButton>
          </Grid>
        </div>
      );
    } else {
      return null
    }

  }
}

TableHotel.defaultProps = {
  tableHeaderColor: "gray"
};

TableHotel.propTypes = {
  classes: PropTypes.object.isRequired,
  tableHeaderColor: PropTypes.oneOf([
    "warning",
    "primary",
    "danger",
    "success",
    "info",
    "rose",
    "gray"
  ]),
  tableHead: PropTypes.arrayOf(PropTypes.string)
};

const mapStateToProps = state => {
  return {
    total_pages: state.listHotelReducer.total_pages,
    listHotel: state.listHotelReducer.listHotel,
    isLoading: state.listHotelReducer.isLoading,
    getSuccess: state.listHotelReducer.getSuccess,
    errorGet: state.listHotelReducer.errorGet,
  }
}

const redux = connect(mapStateToProps)(TableHotel)

export default withStyles(tableStyle)(redux);