import React, { Component } from "react";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
import { connect } from 'react-redux'
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import {
  TextField,
  Grid,
  Checkbox,
} from "@material-ui/core";
import TableRoom from "../../../components/Table/TableRoom";
import MySnackBar from "../../../components/SnackBar";
import { createHotel } from "../../../redux/actions/detailHotelAction";

const LAT_REGEX = /^[+-]?(([1-8]?[0-9])(\.[0-9]{1,6})?|90(\.0{1,6})?)$/;
const LONG_REGEX = /^[+-]?((([1-9]?[0-9]|1[0-7][0-9])(\.[0-9]{1,6})?)|180(\.0{1,6})?)$/;
const PHONE_REGEX = /^\(?([0-9]{3})\)?[-.\s]?([0-9]{3})[-.\s]?([0-9]{4})$/

class CreateHotel extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: "",
      address: "",
      description: "",
      wifi: false,
      laundry: false,
      bar: false,
      lat: "",
      errorLat: null,
      long: "",
      errorLong: null,
      status: false,
      active: true,
      errorPhone: null
    };
  }

  componentDidMount() {
    const { accessToken } = this.props
    console.log('accessToken', accessToken)
  }

  makeBody = () => {
    const logo =
      "https://staticproxy.mytourcdn.com/1000x600,q90/resources/pictures/hotels/18/mxn1402038688_khach-san-minh-duc.jpg";
    const photo = [
      "https://du-lich.chudu24.com/f/m/1509/23/khach-san-cong-doan-sapa-2.jpg",
      "http://media.dulich24.com.vn/khachsan/minh-duc-hotel-010410/minh-duc-hotel-1010410.jpg"
    ];
    const body = {
      address: this.state.address,
      description: this.state.description,
      facilities: {
        bar: this.state.bar ? this.state.bar : false,
        fitness: this.state.fitness ? this.state.fitness : false,
        laundry: this.state.laundry ? this.state.laundry : false,
        wifi: this.state.wifi ? this.state.wifi : false
      },
      location: {
        coordinates: [parseInt(this.state.long), parseInt(this.state.lat)],
        type: "Point"
      },
      logo: logo,
      name: this.state.name,
      phone_number: this.state.phone,
      photos: photo,
      status: true,
      active: true
    };
    console.log(JSON.stringify(body))

    return JSON.stringify(body);
  };

  create = () => {
    const { accessToken } = this.props
    const { errorLat, errorLong, errorPhone } = this.state
    if (errorLat || errorLong || errorPhone) {
      return;
    }
    this.props.onCreateHotel(this.makeBody(), accessToken)
  };

  openSnackBar = (type, message) => {
    const obj = {
      variant: type,
      message: message
    };
    this.refs.MySnackBar.handleOption(obj);
    this.refs.MySnackBar.handleOpen();
  };

  onBack = () => {
    this.props.history.goBack();
  };

  componentWillReceiveProps(nextProps) {
    if (nextProps && !!!nextProps.isCreating && !!nextProps.createSuccess && !!!nextProps.createError) {
      this.openSnackBar("success", "To mi thnh cng");
      setTimeout(() => {
        this.onBack();
      }, 1000);
    }
    if (nextProps && !!!nextProps.isCreating && !!!nextProps.createSuccess && nextProps.createError) {
      this.openSnackBar("error", `Cp nht tht bi ! ${nextProps.createError}`);
    }
  }

  render() {
    const { classes } = this.props;
    const {
      name,
      address,
      description,
      wifi,
      laundry,
      bar,
      lat,
      long,
      status,
      active,
      errorLat,
      errorLong,
      phone,
      errorPhone
    } = this.state;
    return (
      <GridContainer>
        <GridItem xs={12} sm={12} md={12}>
          <Card>
            <CardHeader color="primary">
              <h4 className={classes.cardTitleWhite}>To khch sn mi </h4>
            </CardHeader>
            <CardBody>
              <GridContainer>
                <GridItem xs={12} sm={12} md={3}>
                  <TextField
                    id="standard-name"
                    label="Tn khch sn"
                    value={name}
                    onChange={event =>
                      this.setState({ name: event.target.value })
                    }
                    margin="normal"
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={6}>
                  <TextField
                  fullWidth
                    id="standard-address"
                    label="a ch"
                    value={address}
                    onChange={event =>
                      this.setState({ address: event.target.value })
                    }
                    margin="normal"
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={3}>
                  <TextField
                    id="standard-address"
                    label="S in thoi "
                    value={phone}
                    onChange={event => {
                      console.log(PHONE_REGEX.test(event.target.value));
                      if (!PHONE_REGEX.test(event.target.value)) {
                        this.setState({
                          phone: event.target.value,
                          errorPhone: "sai ri "
                        });
                      } else {
                        this.setState({ phone: event.target.value, errorPhone: null });
                      }
                    }}
                    margin="normal"
                    error={errorPhone}
                  />
                </GridItem>
              </GridContainer>
              <GridContainer
                style={{
                  alignItems: "flex-end",
                  paddingTop: 15,
                  paddingBottom: 15
                }}
              >
                <h3
                  style={{
                    fontSize: 16,
                    marginLeft: "15px",
                    marginTop: 0,
                    marginBottom: 10,
                    padding: 0,
                    marginRight: "15px"
                  }}
                >
                  To 
                </h3>
                <GridItem xs={12} sm={12} md={2}>
                  <Grid container alignItems="center" direction="row">
                    <TextField
                      id="standard-name"
                      label="V "
                      value={lat}
                      onChange={event => {
                        console.log(LAT_REGEX.test(event.target.value));
                        if (!LAT_REGEX.test(event.target.value)) {
                          this.setState({
                            lat: event.target.value,
                            errorLat: "sai ri "
                          });
                        } else {
                          this.setState({ lat: event.target.value, errorLat: null });
                        }
                      }}
                      margin="normal"
                      error={errorLat}
                    />
                  </Grid>
                </GridItem>
                <GridItem xs={12} sm={12} md={2}>
                  <Grid container alignItems="center" direction="row">
                    <TextField
                      id="standard-name"
                      label="Kinh "
                      value={long}
                      onChange={event => {
                        if (!LONG_REGEX.test(event.target.value)) {
                          this.setState({
                            long: event.target.value,
                            errorLong: "sai ri "
                          });
                        } else {
                          this.setState({ long: event.target.value, errorLong: null });
                        }
                      }}
                      margin="normal"
                      error={errorLong}
                    />
                  </Grid>
                </GridItem>
              </GridContainer>
              <GridContainer
                style={{
                  alignItems: "center",
                  paddingTop: 15,
                  paddingBottom: 15
                }}
              >
                <h3
                  style={{
                    fontSize: 16,
                    marginLeft: "15px",
                    marginTop: 0,
                    marginBottom: 0,
                    padding: 0
                  }}
                >
                  Dch v
                </h3>
                <GridItem xs={12} sm={12} md={3}>
                  <Grid container alignItems="center" direction="row">
                    <Checkbox
                      checked={wifi}
                      onChange={event =>
                        this.setState({ wifi: event.target.checked })
                      }
                    />
                    <h3
                      style={{
                        fontSize: 16,
                        padding: 0,
                        marginLeft: "20px",
                        marginBottom: 0,
                        marginTop: 0
                      }}
                    >
                      Wifi
                    </h3>
                  </Grid>
                </GridItem>
                <GridItem xs={12} sm={12} md={3}>
                  <Grid container alignItems="center" direction="row">
                    <Checkbox
                      checked={laundry}
                      onChange={event =>
                        this.setState({ laundry: event.target.checked })
                      }
                    />
                    <h3
                      style={{
                        fontSize: 16,
                        padding: 0,
                        marginLeft: "20px",
                        marginBottom: 0,
                        marginTop: 0
                      }}
                    >
                      Laundry
                    </h3>
                  </Grid>
                </GridItem>
                <GridItem xs={12} sm={12} md={3}>
                  <Grid container alignItems="center" direction="row">
                    <Checkbox
                      checked={bar}
                      onChange={event =>
                        this.setState({ bar: event.target.checked })
                      }
                    />
                    <h3
                      style={{
                        fontSize: 16,
                        padding: 0,
                        marginLeft: "20px",
                        marginBottom: 0,
                        marginTop: 0
                      }}
                    >
                      Bar
                    </h3>
                  </Grid>
                </GridItem>
              </GridContainer>
              <GridContainer
                style={{
                  alignItems: "center",
                  paddingTop: 15,
                  paddingBottom: 15
                }}
              >
                <h3
                  style={{
                    fontSize: 16,
                    marginLeft: "15px",
                    marginTop: 0,
                    marginBottom: 0,
                    padding: 0
                  }}
                >
                  Trng thi
                </h3>
                <GridItem xs={12} sm={12} md={4}>
                  <Grid container alignItems="center" direction="row">
                    <Checkbox
                      checked={active}
                      onChange={event =>
                        this.setState({ active: event.target.checked })
                      }
                    />
                    <h3
                      style={{
                        fontSize: 16,
                        padding: 0,
                        marginLeft: "20px",
                        marginBottom: 0,
                        marginTop: 0
                      }}
                    >
                      ang hot ng
                    </h3>
                  </Grid>
                </GridItem>
                <GridItem xs={12} sm={12} md={4}>
                  <Grid container alignItems="center" direction="row">
                    <Checkbox
                      checked={status}
                      onChange={event =>
                        this.setState({ status: event.target.checked })
                      }
                    />
                    <h3
                      style={{
                        fontSize: 16,
                        padding: 0,
                        marginLeft: "20px",
                        marginBottom: 0,
                        marginTop: 0
                      }}
                    >
                      Cn phng
                    </h3>
                  </Grid>
                </GridItem>
              </GridContainer>
              <GridContainer>
                <GridItem xs={12} sm={12} md={12}>
                  <TextField
                    multiline={true}
                    fullWidth={true}
                    id="standard-description"
                    label="M t"
                    value={description}
                    onChange={event =>
                      this.setState({ description: event.target.value })
                    }
                    margin="normal"
                  />
                </GridItem>
              </GridContainer>
            </CardBody>
            <CardFooter>
              <Grid container direction="row">
                <Button color="primary" onClick={this.create}>
                  To
                </Button>
                <Button
                  color="primary"
                  style={{ marginLeft: 20 }}
                  onClick={this.onBack}
                >
                  Quay li
                </Button>
              </Grid>
            </CardFooter>
          </Card>
        </GridItem>
        <MySnackBar ref={"MySnackBar"} />
      </GridContainer>
    );
  }
}

const styles = {
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0"
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none"
  }
};

const mapStateToProps = state => {
  return {
    accessToken: state.profileReducer.accessToken,
    isCreating: state.detailHotelReducer.isCreating,
    createSuccess: state.detailHotelReducer.createSuccess,
    createError: state.detailHotelReducer.createError
  }
}

const mapDispatchToProps = dispatch => {
  return {
    onCreateHotel: (body, token) => {
      dispatch(createHotel(body, token))
    }
  }
}

const redux = connect(mapStateToProps, mapDispatchToProps)(CreateHotel)

export default withStyles(styles)(redux);
