import {
  LOGIN_REQUEST,
  LOGIN_SUCCESS,
  LOGIN_FAILED,
  SIGNUP_REQUEST,
  SIGNUP_SUCCESS,
  SIGNUP_FAILED,
  LOG_OUT,
} from "../../config/constAct";

const initialState = {
  profile: {},
  accessToken: '',

  isLoading: false,
  isSignup: false,
  loginSuccess: false,
  isLoggedIn: false
};

const profileReducer = (state = initialState, action) => {
  switch (action.type) {
    //LOGIN WITH ACCOUNT
    case LOGIN_REQUEST:
      return Object.assign({}, state, {
        isLoading: true,
        loginSuccess: false,
        errorLogin: ""
      });

    case LOGIN_SUCCESS:
      return Object.assign({}, state, {
        isLoading: false,
        profile: action.payload.user,
        accessToken: action.payload.access_token,
        loginSuccess: true,
        isLoggedIn: true
      });

    case LOGIN_FAILED:
      return Object.assign({}, state, {
        isLoading: false,
        loginSuccess: false,
        errorLogin: action.payload
      });

    //SIGNUP
    case SIGNUP_REQUEST:
      return Object.assign({}, state, {
        isSignup: true
      });

    case SIGNUP_SUCCESS:
      return Object.assign({}, state, {
        isSignup: false,
        profile: action.payload
      });

    case SIGNUP_FAILED:
      return Object.assign({}, state, {
        isSignup: false
      });

    case LOG_OUT:
      return initialState;

    default:
      return state;

  }
};

export default profileReducer;
