import {
  LOGIN_REQUEST,
  LOGIN_SUCCESS,
  LOGIN_FAILED,
  SIGNUP_REQUEST,
  SIGNUP_SUCCESS,
  SIGNUP_FAILED,
  ANOTHER_HOST,
  LOG_OUT,
} from "../../config/constAct";
//LOGIN WITH ACCOUNT
export const loginRequest = () => ({
  type: LOGIN_REQUEST
});

export const loginSuccess = data => ({
  type: LOGIN_SUCCESS,
  payload: data
});

export const loginFailed = error => ({
  type: LOGIN_FAILED,
  payload: error
});

export const login = (params) => {
  return async dispatch => {
    dispatch(loginRequest());
    try {
      let config = {
        method: "POST",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json"
        },
        body: params
      };
      let response = await fetch(`${ANOTHER_HOST}/login`, config);
      let json = await response.json();
      console.log("login", json);
      if (json.status) {
        dispatch(loginSuccess(json));
      } else {
        dispatch(loginFailed(json.message));
      }
    } catch (error) {
      console.log("error", error);
      dispatch(loginFailed(error));
    }
  };
};

//SIGNUP
export const signUpRequest = () => ({
  type: SIGNUP_REQUEST
});

export const signUpSuccess = data => ({
  type: SIGNUP_SUCCESS,
  payload: data
});

export const signUpFailed = error => ({
  type: SIGNUP_FAILED,
  payload: error
});

export const signup = (params) => {
  return async dispatch => {
    dispatch(signUpRequest());
    try {
      let config = {
        method: "POST",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json"
        },
        body: params
      };

      let response = await fetch(`${ANOTHER_HOST}/signup`, config);
      let json = await response.json();
      console.log("signup", json);
      if (json.status) {
        dispatch(signUpSuccess(json.user));
      }
      else {
        dispatch(signUpFailed(json.message))
      }
      dispatch(signUpFailed());
    } catch (error) {
      console.log("error", error);
      dispatch(signUpFailed(error));
    }
  };
};

//LOGOUT
export const logoutRequest = () => ({
  type: LOG_OUT
})

export const logout = () => {
  return async dispatch => {
    dispatch(logoutRequest())
  }
}