import {
    GET_USER_DETAIL_REQUEST,
    GET_USER_DETAIL_SUCCESS,
    GET_USER_DETAIL_FAILED,
    GET_HOTEL_MANAGER_REQUEST,
    GET_HOTEL_MANAGER_SUCCESS,
    GET_HOTEL_MANAGER_FAILED,
    DELETE_USER_REQUEST,
    DELETE_USER_SUCCESS,
    DELETE_USER_FAILED,
    GET_LIST_USER_REQUEST,
    GET_LIST_USER_SUCCESS,
    GET_LIST_USER_FAILED,
    MANAGER,
    CLIENT,
    UPDATE_USER_REQUEST,
    UPDATE_USER_SUCCESS,
    UPDATE_USER_FAILED,
    CREATE_USER_REQUEST,
    CREATE_USER_SUCCESS,
    CREATE_USER_FAILED,
    ADD_HOTEL_MANAGER_REQUEST,
    ADD_HOTEL_MANAGER_SUCCESS,
    ADD_HOTEL_MANAGER_FAILED,
    REMOVE_USER_DETAIL,
    DELETE_HOTEL_MANAGER_REQUEST,
    DELETE_HOTEL_MANAGER_SUCCESS,
    DELETE_HOTEL_MANAGER_FAILED,
    LOG_OUT
} from "../../config/constAct";

const initialState = {
    userDetail: {},
    isGetUserDetail: false,
    getDetailSuccess: false,
    errorGetDetail: null,

    hotelManager: [],
    isGetHotelManager: false,
    getHotelManagerSuccess: false,
    errorGetHotelManager: null,
    done: false,

    isAddHotelManager: false,
    addHotelManagerSuccess: false,
    errorAdd: null,

    isDeleting: false,
    deleteSuccess: false,
    errorDelete: null,

    listManager: [],
    listClient: [],
    isGet: false,
    getSuccess: false,
    errorGet: null,

    isUpdate: false,
    updateSuccess: false,
    errorUpdate: null,

    isCreating: false,
    createSuccess: false,
    errorCreate: null,

    idDeleteHotel: false,
    deleteSuccess: false,
    deleteError: null
};

const userDetailReducer = (state = initialState, action) => {
    switch (action.type) {
        //GET USER DETAIL
        case GET_USER_DETAIL_REQUEST:
            return Object.assign({}, state, {
                userDetail: {},
                isGetUserDetail: true,
                getDetailSuccess: false,
                errorGetDetail: null
            })

        case GET_USER_DETAIL_SUCCESS:
            return Object.assign({}, state, {
                userDetail: action.payload,
                isGetUserDetail: false,
                getDetailSuccess: true,
                errorGetDetail: null
            })

        case GET_USER_DETAIL_FAILED:
            return Object.assign({}, state, {
                userDetail: {},
                isGetUserDetail: false,
                getDetailSuccess: false,
                errorGetDetail: action.payload
            })

        //GET HOTEL MANAGER
        case GET_HOTEL_MANAGER_REQUEST:
            return Object.assign({}, state, {
                hotelManager: [],
                isGetHotelManager: true,
                getHotelManagerSuccess: false,
                errorGetHotelManager: null,
                done: false
            })

        case GET_HOTEL_MANAGER_SUCCESS:
            return Object.assign({}, state, {
                hotelManager: action.payload,
                isGetHotelManager: false,
                getHotelManagerSuccess: false,
                errorGetHotelManager: null,
                done: false,
            })

        case GET_HOTEL_MANAGER_FAILED:
            return Object.assign({}, state, {
                hotelManager: [],
                isGetHotelManager: false,
                getHotelManagerSuccess: false,
                errorGetHotelManager: action.payload,
                done: false
            })

        //DELETE USER
        case DELETE_USER_REQUEST:
            return Object.assign({}, state, {
                isDeleting: true,
                deleteSuccess: false,
                errorDelete: null
            })

        case DELETE_USER_SUCCESS:
            return Object.assign({}, state, {
                isDeleting: false,
                deleteSuccess: true,
                errorDelete: null,
                listManager: state.listManager.filter(item => item.id !== action.payload),
                listClient: state.listClient.filter(item => item.id !== action.payload)
            })
        case DELETE_USER_FAILED:
            return Object.assign({}, state, {
                isDeleting: false,
                deleteSuccess: false,
                errorDelete: action.payload
            })

        //GET_LIST_USER
        case GET_LIST_USER_REQUEST:
            return Object.assign({}, state, {
                listManager: [],
                listClient: [],
                isGet: true,
                getSuccess: false,
                errorGet: null
            });

        case GET_LIST_USER_SUCCESS:
            return Object.assign({}, state, {
                listManager: action.payload.filter(item => item.role === MANAGER),
                listClient: action.payload.filter(item => item.role === CLIENT),
                isGet: false,
                getSuccess: true,
                errorGet: null
            });

        case GET_LIST_USER_FAILED:
            return Object.assign({}, state, {
                listManager: [],
                listClient: [],
                isGet: false,
                getSuccess: false,
                errorGet: action.payload
            });

        //UPDATE_USER
        case UPDATE_USER_REQUEST:
            return Object.assign({}, state, {
                isUpdate: true,
                updateSuccess: false,
                errorUpdate: null
            })
        case UPDATE_USER_SUCCESS:
            return Object.assign({}, state, {
                isUpdate: false,
                updateSuccess: true,
                errorUpdate: null,
                userDetail: action.payload
            })
        case UPDATE_USER_FAILED:
            return Object.assign({}, state, {
                isUpdate: false,
                updateSuccess: false,
                errorUpdate: action.payload,
            })

        //CREATE USER 
        case CREATE_USER_REQUEST:
            return Object.assign({}, state, {
                isCreating: true,
                createSuccess: false,
                errorCreate: null
            })
        case CREATE_USER_SUCCESS:
            return Object.assign({}, state, {
                isCreating: false,
                createSuccess: true,
                errorCreate: null
            })
        case CREATE_USER_FAILED:
            return Object.assign({}, state, {
                isCreating: false,
                createSuccess: false,
                errorCreate: action.payload
            })

        //ADD HOTEL MANAGER
        case ADD_HOTEL_MANAGER_REQUEST:
            return Object.assign({}, state, {
                isAddHotelManager: true,
                addHotelManagerSuccess: false,
                errorAdd: null,
            })
        case ADD_HOTEL_MANAGER_SUCCESS:
            return Object.assign({}, state, {
                isAddHotelManager: false,
                addHotelManagerSuccess: true,
                errorAdd: null,
                done: true
            })
        case ADD_HOTEL_MANAGER_FAILED:
            return Object.assign({}, state, {
                isAddHotelManager: false,
                addHotelManagerSuccess: false,
                errorAdd: action.payload,
            })

        //DELETE HOTEL MANAGER
        case DELETE_HOTEL_MANAGER_REQUEST:
            return Object.assign({}, state, {
                idDeleteHotel: true,
                deleteSuccess: false,
                deleteError: null
            })
        case DELETE_HOTEL_MANAGER_SUCCESS:
            return Object.assign({}, state, {
                idDeleteHotel: false,
                deleteSuccess: true,
                deleteError: null,
                done: true
            })

        case DELETE_HOTEL_MANAGER_FAILED:
            return Object.assign({}, state, {
                idDeleteHotel: false,
                deleteSuccess: false,
                deleteError: action.payload
            })

        case REMOVE_USER_DETAIL:
            return initialState

        case LOG_OUT:
            return initialState;

        default:
            return state;

    }
};

export default userDetailReducer;
