import React, { Component } from "react";
import {
  Button,
  Grid,
  TextField,
  Card,
  CardContent,
  CircularProgress
} from "@material-ui/core";
import withStyles from "@material-ui/core/styles/withStyles";
import loginStyle from "../../assets/jss/material-dashboard-react/containers/loginStyle.js";
import CardMedia from "@material-ui/core/CardMedia";
import GridItem from "../../components/Grid/GridItem";
import { connect } from "react-redux";
import { login } from "../../redux/actions/userAction";
import MyDialog from "../../components/Dialog";

const background = require("../../assets/img/sidebar-2.jpg");
// const background = require("../../../public/img/sidebar-2.jpg");

class Login extends Component {
  state = {
    username: "",
    password: ""
  };
  onLoginWithAccount = () => {
    const user = {
      username: this.state.username,
      password: this.state.password
    };
    this.props.onLogin(JSON.stringify(user));
  };

  componentWillReceiveProps(nextProps) {
    if ( nextProps && !!!nextProps.isLoading && !!nextProps.loginSuccess && !!!nextProps.errorLogin) {
      this.refs.MyDialog.handleClickOpen("Login Success", "Login successfully");
      this.props.history.push('/admin/main/dashboard')
    }
    if (nextProps && !!!nextProps.isLoading && !!!nextProps.loginSuccess && nextProps.errorLogin) {
      this.refs.MyDialog.handleClickOpen("Login Failed", nextProps.errorLogin);
    }
  }

  render() {
    const { username, password } = this.state;
    const { history, classes, isLoading } = this.props;
    return (
      <div className={classes.wrapper}>
        <CardMedia
          image={background}
          style={{ width: "100%", height: "100%" }}
        />
        <Grid
          container
          style={{
            position: "absolute",
            backgroundColor: "rgb(0,0,0,0.6)",
            top: 0,
            bottom: 0,
            left: 0,
            right: 0,
            justifyContent: "center",
            alignItems: "center"
          }}
        >
          <GridItem>
            <Grid
              container
              style={{
                justifyContent: "center",
                alignItems: "center"
              }}
            >
              <h1 style={{ color: "white" }}>SmartHotel</h1>
            </Grid>
            <Card>
              <CardContent>
                <TextField
                  disabled={isLoading}
                  fullWidth
                  id="standard-address"
                  label="Username"
                  value={username}
                  onChange={event =>
                    this.setState({ username: event.target.value })
                  }
                  margin="normal"
                  variant="outlined"
                />
                <TextField
                  disabled={isLoading}
                  fullWidth
                  value={password}
                  id="standard-address"
                  label="Password"
                  onChange={event =>
                    this.setState({ password: event.target.value })
                  }
                  type="password"
                  margin="normal"
                  variant="outlined"
                />
                {isLoading ? (
                  <Grid
                    container
                    style={{
                      justifyContent: "center",
                      alignItems: "center",
                      width: "100%",
                      height: 60
                    }}
                  >
                    <CircularProgress size={30} />
                  </Grid>
                ) : (
                  <Grid
                    container
                    style={{
                      justifyContent: "center",
                      alignItems: "center",
                      width: "100%",
                      height: 60
                    }}
                  >
                    <Button
                      fullWidth
                      style={{
                        backgroundColor: "#9D3AB1",
                        color: "white",
                        height: 50,
                        marginTop: 20
                      }}
                      onClick={this.onLoginWithAccount}
                    >
                      ng nhp
                    </Button>
                  </Grid>
                )}
              </CardContent>
            </Card>
          </GridItem>
        </Grid>
        <MyDialog ref={"MyDialog"} />
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    isLoading: state.profileReducer.isLoading,
    errorLogin: state.profileReducer.errorLogin,
    loginSuccess: state.profileReducer.loginSuccess
  };
};

const mapDispatchToProps = dispatch => {
  return {
    onLogin: (data) => {
      dispatch(login(data));
    },
  };
};

const redux = connect(
  mapStateToProps,
  mapDispatchToProps
)(Login);

export default withStyles(loginStyle)(redux);
